header {
    &.shadow-gradient {
        background: linear-gradient(180deg, rgba(0, 0, 0, 0.3) 0%, rgba(0, 0, 0, 0) 100%);
        padding-bottom: 37px;
    }
    &.floating-blur {
        .header-row {
            padding: 6px 0 12px;
            .header-col-right {
                background: rgba(0, 0, 0, 0.05);
                border-radius: 10px;
                padding: 9px 10px 4px 21px;
                backdrop-filter: none;
                @media(min-width:992px) {
                    backdrop-filter: blur(150px);
                }
            }
        }
    }
    &.full-bleed {
        overflow: hidden;
        .header-col-right {
            position: relative;
            .full-bleed-bg {
                display: block;
                width: 300%;
                height: 150%;
                position: absolute;
                left: -31px;
                bottom: -12px;
                background: rgba(0, 0, 0, 0.05);
                border-radius: 10px 0px 0px 10px;
            }
        }
    }
    &.header-dark-skin {
        .header-menu-element {
            .light-menu-icon {
                display: none;
            }
            .header-menu-element-right {
                a {
                    color: #ffffff;
                }
            }
        }
    }
    &.header-light-skin {
        .header-menu-element {
            .dark-menu-icon {
                display: none;
            }
            .header-menu-element-right {
                a {
                    color: #121212;
                }
            }
        }
    }
    .header-col-left {
        &.header-col-left-mobile {
            @media (max-width: 991px){
                z-index: 35;
            }
        }
    }
}

footer {
    .footer-col-left {
        display: block;
        @media (max-width:991px) {

        }
        .logo-wrap {
            padding-right: 45px;
            display: flex;
            flex-direction: row;
            align-items: center;
            @media (max-width:991px) {
                width:100%;
                justify-content: center;
                padding: 0;
            }
        }
    }
    .social-icons {
        ul {
            padding-left: 15px;
            margin-top: 43px;
            margin-bottom: 0px;
            @media (max-width:991px) {
                width: 100%;
                text-align: center;;
            }
        }
        li {
            display: inline-block;
            margin-right: 33px;
            width: 22px;
            @media (max-width:991px) {
                margin-right: 16px;
                margin-left: 16px;
            }
            &:last-child {
                margin-right: 0px;
                @media (max-width:991px) {
                    margin-right: 16px;
                }
            }
            img {
                width: 100%;
            }
        }
    }
}
.cart-menu-modal-submit {
    border: none;
}
.cart-menu-hover-button button {
    border: none;
}

@media (min-width: 992px) {
	.sticky-header {
		position: fixed;
	}
}